import React from "react";
import "./style.css";
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";

import GpsFixedIcon from "@material-ui/icons/GpsFixed";
class Geo extends React.Component {
  render() {
    const mapStyles = {
      height: "100vh",
      width: "100%",
      position: "relative"
    };
    return (
      <div id="geo">
        <div id="main">
          <div id="title-main">
            <b>Jesteśmy tutaj:</b>
          </div>
          <a href="https://www.google.pl/maps/place/Rewa/@54.630823,18.4793296,14z/data=!3m1!4b1!4m5!3m4!1s0x46fdaf3afd7dab39:0x79c87c189cb1d27a!8m2!3d54.6308248!4d18.4968392">
            <div className="road">
              <GpsFixedIcon fontSize="large" /> &nbsp; Dojazd
            </div>
          </a>

          <div id="map" style={mapStyles}>
            <Map
              google={this.props.google}
              zoom={17}
              initialCenter={{ lat: 54.631285, lng: 18.4942815 }}
              style={mapStyles}
            >
              <Marker initialCenter={{ lat: 47.444, lng: -122.176 }}></Marker>
            </Map>
          </div>
        </div>
      </div>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyCmgGkKcgLY3Vs2uTV3o8rwPt-25AInCc4"
})(Geo);
